@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&display=swap");
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
$color-texto: rgb(226, 226, 226);

@mixin width-height {
    min-height: 100vh;
    min-width: 320px;
    max-width: 1920px;
    margin: 0 auto;
}

body {
    @include width-height;
    font-family: "Orbitron", sans-serif;
}

/*clases globales*/

body,
#root,
.App {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overflow{
    overflow: hidden;
}

img {
    width: 100%;
    height: auto;
}
.hover {
    &::before,
    &::after {
        content: "";
        height: 14px;
        width: 14px;
        position: absolute;
        transition: all 0.35s ease;
        opacity: 0;
    }
    &::before {
        content: "";
        right: 0;
        top: 0;
        border-top: 3px solid white;
        border-right: 3px solid white;
        transform: translate(-100%, 50%);
    }
    &:after {
        content: "";
        left: 0;
        bottom: 0;
        border-bottom: 3px solid white;
        border-left: 3px solid white;
        transform: translate(100%, -50%);
    }
    &:hover:before,
    &:hover:after {
        transform: translate(0, 0);
        opacity: 1;
    }
}
#Nasa,
#planetas {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
#planetas,
#Nasa {
    padding: 100px 3rem 0 3rem;
}
.margin {
    margin: 2rem 0;
}

h2,
.titulos {
    font-size: clamp(1.8rem, 5vw, 4rem);
    margin: 2rem 0;
}

h3 {
    font-size: clamp(1.5rem, 3vw, 3rem);
}
h4 {
    font-size: clamp(1.3rem, 2vw, 2rem);
}
p {
    font-size: clamp(1rem, 1.5vw, 1.1rem);
}

.cargando {
    display: flex;
    height: 100vh !important;
    align-items: center;
    justify-content: center;
}
//---------------------------------------------

/*Nav*/

nav {
    @include width-height;
    display: flex;
    position: fixed;
    min-height: max-content;
    top: 0;
    left: 0;
    right: 0;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5rem;
    background-color: transparent;
    z-index: 10;
    background: rgba(4, 4, 4, 0.534);
    backdrop-filter: blur(4px);
    .logo {
        color: $color-texto;
        letter-spacing: 5px;
        font-size: 20px;
    }
    .nav-links {
        display: flex;
        justify-content: space-between;
        text-align: center;
        font-weight: 600;
        &* {
            box-sizing: border-box;
            transition: all 0.35s ease;
        }

        li {
            display: inline-block;
            list-style: outside none none;
            margin: 0.5em 1em;
            padding: 0;
        }

        a {
            padding: 0.5em 0.8em;
            color: $color-texto;
            position: relative;
            text-decoration: none;
            font-size: 20px;
        }
    }
    .nav-active {
        transform: translateX(0%);
    }
    #hamburguesa {
        display: none;
        cursor: pointer;
        div {
            width: 25px;
            height: 3px;
            background-color: white;
            margin: 5px;
            transition: all 0.3s ease;
        }
        .linea1 {
            transform: rotate(-45deg) translate(-5px, 6px);
        }
        .linea2 {
            opacity: 0;
        }
        .linea3 {
            transform: rotate(45deg) translate(-5px, -6px);
        }
    }
}

//-------------------------------------------------------------------------
#Inicio {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(/public/imagenes/fondo10.jpg);
    background-size: cover;
    background-position: center;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    position: relative;
    img {
        width: 100%;
        height: 100%;
    }
    .planetaFuera {
        display: block;
        position: absolute;
        width: 100px;
        height: 100px;
        top: -100px;
    }

    .siguiente {
        display: block;
        position: absolute;
        width: 100px;
        height: 100px;
        top: -100px;
        transform: translateY(150px) scale(2, 2);
        transition: ease-in-out 1s;
        z-index: 9;
    }

    .atras,
    .atras2 {
        display: block;
        width: 200px;
        height: 200px;
        position: absolute;
        top: 0;
        transform: translateY(100vh) scale(5, 5);
        transition: ease-in-out 1s;
    }
    .atras2 {
        transform: translateY(70vh) scale(5, 5);
    }

    .adelante {
        display: block;
        width: 1000px;
        height: 1000px;
        position: absolute;
        bottom: -600px;
        animation-name: fuera;
        animation-fill-mode: forwards;
        animation-duration: 1s;
        @keyframes fuera {
            0% {
                transform: translateY(0);
            }
            100% {
                transform: translateY(100vh);
            }
        }
    }
    .none {
        display: none !important;
    }
    #datos {
        width: 100%;
        height: 100vh;
        display: grid;
        color: $color-texto;
        text-shadow: 2px 2px 2px black;
        letter-spacing: 0px;
        grid-template-areas:
            "nada nada nada"
            "ul titulo info"
            "ul titulo info";
        grid-template-columns: 1fr 2fr 1fr;
        h1 {
            font-size: clamp(2rem, 5vw, 5rem);
        }
        h3 {
            font-size: clamp(1.5rem, 3vw, 2rem);
        }
        p {
            font-size: clamp(1rem, 1.5vw, 1.5rem);
        }
        li {
            font-size: clamp(0.8rem, 1.2vw, 1.2rem);
        }
        .titulo {
            grid-area: titulo;
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            z-index: 8;
            font-size: 1.5rem;
            gap: 2rem;
            line-height: 1.5;
            margin-bottom: 4rem;
        }
        .titulo h1 {
            letter-spacing: 20px;
            text-indent: 20px;
        }
        .ul {
            grid-area: ul;
            display: flex;
            flex-direction: column;
            text-align: center;
            gap: 2rem;
            justify-content: center;
        }
        .info {
            grid-area: info;
            line-height: 2;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            text-align: left;
            align-items: center;
            justify-content: center;
        }

        button {
            background: none;
            border: none;
            color: $color-texto;
            cursor: pointer;
            padding: 0.5em 0.8em;
            color: $color-texto;
            position: relative;
            text-decoration: none;
            font-size: 20px;
            font-weight: 600;
        }

        @mixin animacion($nombre, $porcentaje) {
            animation-name: $nombre;
            animation-fill-mode: forwards;
            animation-duration: 1s;
            @keyframes #{$nombre} {
                0% {
                    opacity: 0;
                    transform: translate3d($porcentaje, 0, 0);
                }
                50% {
                    opacity: 0;
                }
                100% {
                    opacity: 1;
                    transform: none;
                }
            }
        }
        $map: (
            fadeIn: 0,
            fadeOut: 0,
            fadeInLeft: -100%,
            fadeOutLeft: -100%,
            fadeInRight: 100%,
            fadeOutRight: 100%,
        );

        @each $name, $value in $map {
            .#{$name} {
                @include animacion($name, $value);
            }
        }
    }
}

//INICIO

//--------------------------------------------------------------------------------------

/*Planetas*/
#planetas {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(/public/imagenes/fondo3.jpg);
    color: $color-texto;
    gap: 3rem;
    .div-section {
        display: flex;
        letter-spacing: 3px;
        width: 100%;
        gap: 3rem;
        position: relative;
        .div-img {
            position: fixed;
            top: 50px;
            padding-right: 3rem;
        }
        .descripcion {
            align-self: center;
        }
        .lista {
            margin-bottom: 1rem;
            margin-left: 2rem;
        }
        div {
            width: 50%;
        }
        .planetas {
            cursor: pointer;
            width: min-content;
        }
    }
}

//------------------------------------------------------------

/*Api Nasa*/

#Nasa {
    background-image: url(/public/imagenes/fondo5.jpg);
    color: $color-texto;
    #ImagenAStronomica {
        margin: 0 auto 3rem auto;
        max-width: 1000px;
        position: relative;
        h2 {
            text-align: center;
            margin-top: 0;
        }
        .div-img {
            display: flex;
            height: 600px;
            margin: 0 auto;
            text-align: center;
            margin-bottom: 1rem;
            img {
                object-fit: cover;
            }
        }
        #descripcion {
            position: absolute;
            bottom: 10px;
            left: 10px;
        }
    }
    #TierraApi {
        .div-img {
            max-width: 600px;
            margin: 0 auto;
            position: relative;
            #siguiente {
                position: absolute;
                top: 45%;
                right: 0;
            }
            #anterior {
                position: absolute;
                top: 45%;
                left: 0;
            }
            #fecha {
                position: absolute;
                bottom: 10px;
                left: 10px;
            }
            button {
                background: none;
                color: white;
                border: none;
                cursor: pointer;
                font-size: clamp(1.5rem, 4vw, 4rem);
                padding: 5px;
            }
        }
    }
    #MarteApi {
        margin-top: 3rem;
        .camarasDescripcion {
            display: flex;
            flex-direction: column;
            gap: 2rem;
        }
        .Info {
            margin: 1rem 0;
        }
        .camaras {
            display: grid;
            align-content: center;
            grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
            gap: 20px;
            .fotos {
                display: flex;
                margin: 0 auto;
                .div-fotos {
                    width: 200px;
                    position: relative;
                    color: white;

                    p {
                        position: absolute;
                        left: 10px;
                        bottom: 10px;
                        font-size: 0.8rem;
                    }
                }
            }
        }
    }
}

//Media queris

@media screen and (max-width: 1000px) {
    nav {
        padding: 1rem 2.5rem;
    }
    #planetas,
    #Nasa{
        padding: 100px 2.5rem 0 2.5rem;
    }
    //INICIO
    #Inicio {
        .siguiente {
            width: 15vw;
            height: 15vw;
            top: -50px;
            transform: translateY(80px) scale(2, 2);
        }
        .atras,
        .atras2 {
            width: 33vw;
            height: 33vw;
            top: 0;
            transform: translateY(100vh) scale(3, 3);
        }
        .adelante {
            width: 100vw;
            height: 100vw;
            position: absolute;
            bottom: -70vw;
        }
    }
}

@media screen and (max-width: 768px) {
    nav {
        background: rgba(4, 4, 4, 0.911);
        backdrop-filter: blur(4px);
        position: fixed;
    }
    .nav-links {
        position: absolute;
        right: 0px;
        top: 8vh;
        height: 92vh;
        width: 100% !important;
        background: rgba(4, 4, 4, 0.911);
        backdrop-filter: blur(4px);
        flex-direction: column;
        align-items: center;
        justify-content: space-around !important;
        transform: translateX(100%);
        transition: transform 0.5s ease-in;
    }
    .nav-links li {
        opacity: 0;
    }
    #hamburguesa {
        display: block !important;
    }
    #planetas,
    #Nasa {
        padding: 100px 1.5rem 0 1.5rem;
    }
    nav {
        padding: 1rem 1.5rem;
    }
    /*INICIO*/

    #Inicio {
        #datos {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 320px;
            .ul {
                display: none;
            }
        }
    }

    //planetas
    #planetas {
        .div-section {
            flex-direction: column;
            align-items: center;
            width: 100%;
            gap: 1rem;
            .relleno {
                display: none;
            }
            .div-img {
                position: relative;
                top: 0;
                padding-right: 0;
                width: 70%;
            }
            .descripcion {
                width: 100%;
            }
        }
        gap: 1rem;
    }
    //api-nasa
    #ImagenAStronomica {
        .div-img {
            height: 400px !important;
        }
    }
}
@media screen and (max-width: 426px) {
    #ImagenAStronomica {
        .div-img {
            height: 300px !important;
        }
    }
}

@keyframes navLinkFade {
    from {
        opacity: 0;
        transform: translateX(50px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.img-carga {
    align-self: center;
    justify-self: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}
